.auth-container {
    text-align: center;
    max-width: 300px;
    margin: auto;
  }
  
  .google-sign-in, .email-sign-in {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 24px;
    font-size: 16px;
    font-weight: 500;
    padding: 10px;
    width: 80%;
    cursor: pointer;
    transition: background-color 0.2s ease-in-out;
  }
  
  .google-sign-in {
    background-color: white;
    border: 1px solid #dadce0;
    color: #3c4043;
  }
  
  .google-sign-in:hover {
    background-color: #f8f9fa;
  }
  
  .email-sign-in {
    background-color: white;
    border: 1px solid #dadce0;
    color: #3c4043;
  }
  
  .email-sign-in:hover {
    background-color: #f8f9fa;
  }
  
  .google-logo, .email-logo {
    width: 30px;
    margin-right: 10px;
  }
  
  .divider {
    margin: 15px 0;
    font-size: 14px;
    color: #888;
  }
  
  .auth-input {
    display: block;
    width: 100%;
    padding: 10px;
    margin: 5px 0;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .error-message {
    color: red;
    margin-top: 10px;
  }
  